/* fira-sans-condensed-300 - latin */
@font-face {
    font-family: 'Fira Sans Condensed';
    font-style: normal;
    font-weight: 300;
    src: url('fira-sans-condensed/fira-sans-condensed-v10-latin-300.eot'); /* IE9 Compat Modes */
    src: local(''),
         url('fira-sans-condensed/fira-sans-condensed-v10-latin-300.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url('fira-sans-condensed/fira-sans-condensed-v10-latin-300.woff2') format('woff2'), /* Super Modern Browsers */
         url('fira-sans-condensed/fira-sans-condensed-v10-latin-300.woff') format('woff'), /* Modern Browsers */
         url('fira-sans-condensed/fira-sans-condensed-v10-latin-300.ttf') format('truetype'), /* Safari, Android, iOS */
         url('fira-sans-condensed/fira-sans-condensed-v10-latin-300.svg#FiraSansCondensed') format('svg'); /* Legacy iOS */
  }
